.header__container {
  max-width: 320px;
  padding: 22px 20px 0;
  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding: 20px 60px 0 70px;
  }
  @media screen and (min-width: 1440px) {
    display: flex;
    max-width: 1440px;
    height: auto;
    padding: 40px 80px 0;
    margin: 0 auto;
  }
}

